import React, { Component } from 'react';
import head from '../head.png'
import cat from '../cat.png'
import github from '../github.png'
import linkedin from '../linkedin.png'
import angellist from '../angellist.png'
import { Button, Nav, NavItem, NavLink, Navbar } from 'reactstrap';

export default class SplashScreen extends Component {
  constructor(props){
    super(props);
    this.state = {
      img : head,
      imgStyle: {
        width: 256,
        height: 301
      }
    }
  }

  handleClick = () => {
    if (this.state.img === head) {
      this.setState({
        img: cat, 
        imgStyle: {
          width: 300,
          height: 300,
          padding: 50
        }
      })
    } else {
      this.setState({
        img: head, 
        imgStyle: {
          width: 256,
          height: 301,
        }
      })
    }
  }

  render() {
    return (
      <div className="App">
        <Navbar color="dark" dark expand="md">
        <Nav>
          <NavItem>
            <NavLink style={{color: 'white'}} href=" ">Home</NavLink>
          </NavItem>
          <NavItem>
            <NavLink style={{color: 'white'}} href="recipes">Recipes</NavLink>
          </NavItem>
        </Nav>
        <Nav className="ml-auto" navbar>
          <div style={{'display': 'flex','flex-direction':'row', 'align-items': 'center'}}>
            <p style={{color:'grey', margin:10}}>Check Me Out:</p>
            <NavItem>
                <NavLink href="https://github.com/atwoosnam">
                  <img src={github} style={{'width':20, 'height':20}}></img>
                </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="https://www.linkedin.com/in/andrew-t-woosnam/">
              <img src={linkedin} style={{'width':20, 'height':20}}></img>              
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="https://angel.co/andrew-t-woosnam">
              <img src={angellist} style={{'width':20, 'height':20}}></img>              
              </NavLink>
            </NavItem>
          </div>
          </Nav>
        </Navbar>
        <header className="App-header">
          <div style={{'flex-direction':'column'}}>
            <h2>Welcome to the best web app</h2>
            <h2>in the world!</h2>
          </div>
          <div style={{height:300}}>
            <img style={this.state.imgStyle} src={this.state.img} className="App-logo" alt="logo" />
          </div>
            <span>Prepare to be dazzled... </span>
            <Button style={{margin:20}} onClick={this.handleClick} color="primary" block={false}>Dazzle Me</Button>
      </header>
     </div>
    )
  }
}
